.projecto {
    
    text-align: center;
    margin-top: 100px;

    .projectoTXT {

        text-align: center;

        h3 {
            font-size: 30px;

        }

        p {
            margin: 25px 180px 0 180px;
            line-height: 25px;
            color: #919191;
        }
    }

    .alçados {
        margin: 50px 100px 0 100px;

        img {
            width: 700px;
        }
    }


    .plantasIMGS-container {

        display: flex;
        align-items: center;
        place-content: center;
    }

    .content{

        position: relative;
        margin: auto;
        overflow: hidden;
        .content-overlay{

            background: rgba(0,0,0,0.7);
            position: absolute;
            height: auto;
            width: 250px;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            opacity: 0;
            -webkit-transition: all 0.4s ease-in-out 0s;
            -moz-transition: all 0.4s ease-in-out 0s;
            transition: all 0.4s ease-in-out 0s;      
        }

        &:hover {
            .content-overlay {
                opacity: 1;
            }

            .content-details {
                top: 50%;
                left: 50%;
                opacity: 1;
            }
        }

    }

    .content-image {
        width: 250px; 
    }

    .content-details {
        position: absolute;
        text-align: center;
        padding-left: 1em;
        padding-right: 1em;
        width: 100%;
        top: 50%;
        left: 50%;
        opacity: 0;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        -webkit-transition: all 0.3s ease-in-out 0s;
        -moz-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s;
    
        
        h3 {
            color: #fff;
            font-weight: 500;
            letter-spacing: 0.15em;
            margin-bottom: 0.5em;
            text-transform: uppercase;
            font-size: 1em;
        }
    
        p {
            color: #fff;
            font-size: 0.8em;
        }
    }

    .fadeIn-bottom {
        top: 80%;
    }


}




