.hero {
    margin-top: 50px;
    padding: 135px 0 135px 0;
    text-align: center;
    width: 100%;
    height: 520px;
    position: relative;

    h1 {
        font-size: 70px;
    }

    p{
        font-size: 25px;
        font-style: italic;
        color: #919191;
        margin-top: 30px;
        margin-bottom: 180px;

    }
    

    .cta1 {
        color: #919191;
        border-style: solid;
        border-width: 1px;
        border-radius: 25px;
        padding: 10px;

        &:hover {
            color: orangered;
        }
        
    }

    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 520px;
        background-image: url(../img/pattern.png);
        background-size: cover;
        background-repeat: no-repeat;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        opacity: 0.1;
    }
}