header {

	display: flex;
	align-items: center;
	width: 100%;
	justify-content: space-between;
	padding: 0 30px 0 30px;
	width: 100%;
	

	img {
		width: 180px;
	}

	nav {

		ul {
			display: flex;
			align-items: center;
			
			li {
				margin: 0 0 0 40px;

				a {
					color: #919191;
					text-decoration: none;

					&:hover {
						color: orangered;
					}
				}


			}
			
		}

	}

	


}